import React from "react";
import {
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Button,
  Grid,
  TextField,
  Typography,
} from "@mui/material";
import { useForm } from "react-hook-form";
import { Spin, notification } from "antd";
import { postCompanies, putCompanies } from "requests";

const ModalCompany = (props) => {
  const { open, setOpen, setLoading, loading, data, reloadFunction } = props;

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const sendRegister = async (items) => {
    setLoading(true);
    try {
      if (data) await putCompanies(items, data._id);
      else await postCompanies(items);
      reloadFunction();
      setOpen(false);
      notification["success"]({
        message: `Éxito!`,
        description: `La empresa se ${
          Boolean(data) ? "actualizó" : "registró"
        } correctamente.`,
      });
    } catch (error) {
      notification["error"]({
        message: `Ocurrió un error al realizar la operación.`,
      });
    } finally {
      setLoading(false);
    }
  };

  return (
    <Dialog open={open} fullWidth maxWidth="md">
      <DialogTitle>
        <Typography variant="subtitle1" align="center">
          <b>{Boolean(data) ? "EDITAR" : "AGREGAR"} EMPRESA</b>
        </Typography>
      </DialogTitle>
      <Spin spinning={loading}>
        <form onSubmit={handleSubmit(sendRegister)} autoComplete="off">
          <DialogContent>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <TextField
                  label="NOMBRE DE LA EMPRESA"
                  autoFocus
                  fullWidth
                  defaultValue={Boolean(data) ? data.name : ""}
                  error={Boolean(errors?.name ?? false)}
                  {...register("name", { required: true, maxLength: 80 })}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  label="IMAGEN URL"
                  fullWidth
                  defaultValue={Boolean(data) ? data.logo : ""}
                  error={Boolean(errors?.logo ?? false)}
                  {...register("logo", { required: true })}
                />
              </Grid>
            </Grid>
          </DialogContent>
          <DialogActions>
            <Button
              variant="outlined"
              color="secondary"
              onClick={() => setOpen(false)}
            >
              CANCELAR
            </Button>
            <Button variant="contained" color="primary" type="submit">
              GUARDAR
            </Button>
          </DialogActions>
        </form>
      </Spin>
    </Dialog>
  );
};

export default ModalCompany;
